                    <header class="entry-header">
                        <!-- 标题输出 -->
                        <h1 class="entry-title">深圳合租记</h1>
                        <hr>
                        <div class="breadcrumbs">
                            <div id="crumbs">最后更新时间：2021年08月29日</div>
                        </div>
                    </header>
                    <!-- 正文输出 -->
                    <div class="entry-content">
                        <p>@[TOC]</p>
                        <h4 id="引言：">引言：</h4>
                        <p>2020年8月10号，从郑州抵达到深圳！！！</p>
                        <h4>始</h4>
                        <p style="text-indent: 2em;">不做长篇言论，直接入题。2020年8月9号，和前端几个同学决定一块出发到深圳，和张云鹏老师一块坐火车，晚上老师给我们上了一课，感觉似乎有点悬乎，但我们还是鼓着勇气决定去拼了一把。长达十几个小时的路程，晚上倒是有点疲惫 ，一觉睡到了天亮，最后还是因为车厢内有千年一遇的呼噜王 ，迫使我连懒觉真的睡不下去了，然后下床去看外边的风景。</p>
                        <hr>
                        <h4>中</h4>
                        <pre><code> ~~~~~长达十几个小时的路程终于到达了深圳，老师领着我们出去了之后做了好长时间的地铁，在地铁上，见到了南方各种各样plmm，来到了一线城市中，什么都没有感觉到，只感觉到了一股股的压力，在地铁中的人不一定都是高高兴兴的过来，高高兴兴的回去，在他们的眼里能够感觉得到，有一种压迫感，看着他们打着电话还笑嘻嘻的，这种笑嘻嘻的是那种开开心心的笑嘻嘻吗，谁也猜不准他是在应付着谁，也许是自己的客户，也许是自己的上司，更希望是对自己家人的笑嘻嘻。</code></pre>
                        <hr>
                        <h4>抵达</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">做了好长时间的地铁，终于下来了，8月份的天气对于深圳还算可以，毕竟深圳是一个恒温的城市，每天都是这个温度，还是很不错的，不算太热，也不算太冷，拉着重重的行李箱，背着重重的鞋 ，还拖拉着一个拖鞋，这种天气走很长时间还是挺累挺热的，似乎感觉到了绝望，自己在大城市中打拼真的不是那么好拼的，当时心里一直在想奥利给，真的是没办法，终于老师带着我们五个来到了一个8人间的宿舍，还是那么的挤，房价还是那么的贵，刚来到这里，坐火车的400多块钱都是问家人要的，实在不想再去问家人要钱，但最后也是没办法，还是要了1500块钱交了房租，忽然有种想放弃的感觉，曾经和家人说过的去大城市发展，现在一开始却问家人要那么多的钱，实在于心不忍，住着这既拥挤又贵的宿舍，但又不得不住，也许这就是大城市吧，先苦后甜，也是一点点打拼而来的，谁一开始不是努力打拼着，心里无时无刻的想，不要急，慢慢的造，人有多大胆，定有多大产。加油！！！奥利给。</p>
                            <p  style="text-indent: 2em;">来到这里，生活用品什么都没有，都要去出去买，也是要花钱的喽，出去简简单单的买了一点东西，牙刷牙膏，盆等等，回去之后，今天算是很疲惫的一天，也是简简单单的把床铺了一下，倒在床上不那么一会就睡着了，毕竟第二天早上还要早起投递简历，找工作的风波就这样到来了</p>
                        </blockquote>
                        <hr>
                        <ol start="2">
                            <li>面试史
                                <blockquote>
                                    <p style="text-indent: 2em;">第二天早上，也就是8月11号，前天晚上老师说今天要在前程无忧投递上300份简历，这个东西也是第一次用 ，还有boss直聘，智联招聘，根本不知道各个公司是什么样的一个进展，我们在前程无忧做了一个筛选，把自己合适的职位筛选出来，好在这东西能一次投递50份简历，搜上三下，150份就这样投递出去了，虽然是筛选，但什么样的公司都有，不过一会，室友的电话开始响了起来，一个比一个忙，收到了各个公司的邀请面试，而我的电话还是一往平静，没有任何消息，前程无忧投递完之后，也去了boss直聘，boss直聘的是直接可以和各个公司的前台联系，前台感觉合适的话，就直接面试邀请，还是挺不错的，几个小时下来，他们都各自有了后一天的面试邀请，在吃饭的时候，一个公司好像注意到了我，但他们的效率是真的慢，等的我燥火，吃完饭之后，终于有一家公司给我打电话了，也就是后一天早上的面试，更巧的是，我和我的室友去了同一家公司，下午依然在联系，但效果几乎都不是太好。</p>
                                    <p style="text-indent: 2em;">第二天是上午10点的面试，我和我室友也不敢迟到，老早的起来了，那天还是下着雨，确实感觉到了一丝丝的心累，到了那地方之后，我和我室友都傻眼了，这个公司竟然是在一个居民楼上边，但是在云和的时候，张鹏浩说如果是居民楼的什么什么公司最好不要去，有可能是小黑屋哦，不过我们两个想 ，来都来了，就要去上面看一看，我问了一下张云鹏老师，他说的没有关系，可以去，我们也没有管那么多，直接就进去了，进去之后我们两个就傻眼了，这个公司里一共才见到3个人，真的是无语了，更懵逼的是，老板直接让我们俩上机操作，虽然我们俩是培训机构培训出来的，但实力还是不允许，就像老师之前说过的，虽然是培训出来的，但我们出去找工作真的是什么都不是，我现在终于相信这句话了</p>
                                    <p style="text-indent: 2em;">长达1个多小时的上机，真正感觉到了困难，在学习的时候那是写的什么项目，什么都不算，到了真正的实战，真的是看一脸懵逼，琢磨了好久，才算是写出来一个简单的渲染，但这点并不代表什么，老板根本不看在眼里，最后老板以委婉的接口让我们走了，说的是什么他们需要实力过硬的，我当时就想去你妹的吧，也不看看自己是什么公司，还要实力过硬的，垃圾的一批，我们两个下楼之后，外边突然下起了大雨，那是真的大，还好带了伞，天空灰蒙蒙的下着雨，我们两个的心情都不是太好，下午还有各自的一个面试，我们两个吃过饭之后就分开头去面试了，下午的是在南山区，在当时的龙岗区过去那边是真的远，也只好吃过饭直接打了公交过去，刚上去公交没多久，就又下起了大雨，在车上坐了大概将近两个小时，下车之后大雨还在下，只好一手拿手机导航，一手拿雨伞，生活不得不迫使我去坚持，到了楼下之后，问了一个人之后才知道这个公司的位置，乘电梯上去了，刚到里边，前台直接给我了一份面试题，感觉这才是真正的面试，上午的那家那是啥，面试题很简答，我一会写完了， 也有一两道难得，不过都让我百度了出来，等了好久之后，才有人让我去面试，第一次真正面试的我以为面试时多么严肃的一件事，然而没有什么好害怕的，我甚至一点紧张都没有，虽然是不害怕，但让这个人直接给我问懵逼了，毕竟自己是没有在公司经历过的，还有我们的简历也是造假出来的，不造假的话面试的机会是很少的，我们也只能搏一搏，假装自己是在公司待过一年的人，但我第一次正儿八经的面试，好像这个人看出了我不是真正的一年的经验，毕竟写项目根本不着边，随便说了几分钟后，这个人就直接说让我回去等通知，我知道一般这样的结果是不会有结果，但我还是自信满满，他爱要不要，我就当是经历了，仅仅面试了一天，我差不多已经找到了感觉，回去之后继续投递简历，等待邀请面试，第二天上午的面试我依然还是和我室友面试的同一家。</p>
                                </blockquote>
                            </li>
                        </ol>
                        <hr>
                        <h4>经验搏一搏</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">犹如昨天晚上老师说的一样，对于在公司面试，要把自己的信息拿出来，没有过没关系，关键在于吸收经验。另一方面，既然都假装了，要假装出自己很牛逼的样子，这样才可能让面试的人信服你，哪怕你是真的不会，也要硬气的去说一说，错就错了，没有什么关系。</p>
                            <p style="text-indent: 2em;">我怀揣着信心，和室友来到了这一家公司，这家公司也是一二十人的样子，但工资给的也不是太高，我直接进去碰一碰运气，进来的这个人说话有点傻逼，眼睛还那么有点斜，他问我的问题也都是基础的，偶尔也会有那么一点难得，关于这点难得，我直接硬气的和他怼，他说我说的这个答案对吗，我说的对，其实这是不对的，但我还是这样去说，因为我不可能后退，不要让他以为自己很弱的样子，毕竟气场不能认输，和这个人对峙了几十分钟后，然后他们的人事来了，然后又哔哔了好几分钟，然后让我等通知，这次的面试我很舒畅，毕竟我的气场拿出来了，输赢倒是无所谓，然后我室友进去面试了，我在外面也是等了好长时间，最后他也出来了，感觉还都挺不错，因为我面试了一天之后，大概的道理和规矩也都差不多明白了，公司里边的怎么去面试一个人，我似乎也是明白了，这一家我感觉有70的几率，我把能说的都说了 ，气场这一块我也是拿的死死的，从不输于任何人，这一家算是结束了，然后准备吃饭去下一家，刚吃过饭，外面就又下起了大雨，似乎有点难，再难也要坚持下去，吃完饭看了一下手机，下一家离的也是太远了，就在这时，又有一家公司给我打电话，此时我的面试已经排到了下周的周一。</p>
                        </blockquote>
                        <h4>万万没想到</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                挂了电话之后，灰蒙蒙的sky，雨下的越来越大了，我手中的小umbrella都快招架不住了，一手撑着伞，一手拿着手机看那该死的高德瞎几把导航的地图，大城市中那么好的信号，但网真是差的一批，就这样跟着这不好使的地图终于找到了一个公交车站牌，这一家的公司很远，关键是我当时从龙岗的最东边赶到南山的最南边，十万八千里的距离，差不多就是横穿了整个深圳的样子，中间我转了好几辆公交车，远的确实过分，到了地方之后，眼前的一幕让我大吃一惊，好家伙，这地方也他么的太繁华了吧，别说进公司了，就只看见眼前这情景，就让我感觉到了压力山大，跟着导航又走了好几百米，正好下车时雨已经停了，当时的天气很凉爽，到了公司楼下，好好的感受了这真正大城市的样子，这个公司是入驻在一个名字叫卫星大厦的大厦中，关于这个大厦为什么叫卫星大厦，我也不清楚，看起来是很牛逼的样子，然后我进去问门口的保安，我问了一下这个公司是不是在这个大厦中，当然他可能也不知道，因为大厦中入驻的公司太多了，因为我是真的不知道这里边的实情是什么样的，我配合着保安的登记以后，我进去了电梯内，这公司是在8楼802，到了8楼，看着这高端上档次的写字楼，真的是，无法描述。
                            </p>
                        </blockquote>
                        <h4>成功之日</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                我进入这个公司之后，有一个人看到了我，然后问我是不是面试的，我一口答应了，然后他让我稍等一会，一会后，应该是前台的小姐姐让我进入了会议室，开始面试，进来的面试官是一个比我大几岁的，他拿着我的简历开始问我问题，从这个面试官的语言中我能体会到他应该是一个很会体谅的一个人，非常不错，然而我这次来并没有打着面试成功的心理来的，因为这个地方离我住的地方太远了，而且这个地方的生存环境已经让我感受到了压力，以前说大城市有压力的，只是说说而已，并没有那么严重，而当你真正去到了大城市当中你会发现，其实压力还是挺大的，和这个面试官聊了一点时间之后会发现，这个面试官没有去难为人的意思，因为大家都明白，每个人都是这样进来的，打工人没必要去互相为难，反正比之前的任意一家公司的面试官好上一百倍，当然我也有没有回答出来的问题，而他是慢慢的给我解释了一遍是为什么，我也听的很认真，也是虚心的学到了，完后，他又让我等了一会，接下来又来了一个面试官，他说他是这个公司技术部的技术经理，从外表上看起来很普普通通，也许他有着内在的实力，他先介绍了一遍他自己，我又介绍了一遍我自己，之后他让我说了一个vue的双向绑定原理，天哪，在学习的时候，我都是一直用，从来没有去了解过什么什么原理，我很不好意思的坑了几句，很明显是不对的，他说这么基础的问题都不会，我也没有说什么，因为这家公司的面试官人都不错，我没有想怼的意思，令我没有想到的是，他好像对我很认可，我们也是聊了很长时间的家常，因为他说他像我这么大的时候，已经外漂好多年了，到今，也是有一二十年的社会经验了，而他经历的人也是有很多，就连商丘的人他也是认识了不少，河南话也会说那么少许，况且他是一个湖南人，然后他让我摘下口罩说让我们互相看一看，免得以后在公司碰到以后还是那么的生疏，我在想，他是什么意思，难道说我面试成功了，而且我并没有打着面试成功的心理来的，这地方确实有点那个什么，我还是不太相信，他说着我的年龄还是很小的，在他看来，一番对话之后，我心理感觉都挺好的，虽然这次我回答的不是很好，不像之前，我虽然回答的不好，但我还是坚持了我的意见，关键还是看公司面试官的人品，在这之前的所有公司的面试官，我他么真想干死他，感觉自己好牛逼的样子，然后这个经理又让我稍等了一会，具体什么情况，我也不知道，我就在会议室待了一会，过了一会，又来了一个前端面试的，这个前台的让我先去大厅坐着等一会，我四处望了望，这个公司整体氛围还是不错的，还看见了两个好看的小姐姐，不一会，这里的人资把我叫到了会议室，我一开始想着自己介绍一遍来着，他说的不用了，然后又了解了我的生活，等一些面试技巧中常问到的问题，我回答的都很好，接着就谈到了薪资的问题，我的面试单上面写着6-7k，这个工资其实并不高，我也是对我自己衡量了一下，决定了就是这个数目，我心里一想，看来这次有戏了，但又一想，这地方是很让我感到不适，并不是很想来的意思，我并没有拒绝，先答应了下来，最后说他们的前台过会会给我打个电话通知我一下，然后给我发offer，就这样，整个面试流程过去了，我满怀欣喜又不太喜欢的走了，然后去了趟厕所，看到这厕所，我真是，这厕所简直比我住的地方还要香，尿了一泡之后，我准备离开了，我走到电梯口，看到刚刚进去面试的那一位，正在拿着他自己记的笔记背着，还是css的那一块，我只是感到了一种心酸和努力付出获得回报的心情，我走出了这栋大厦，拿起高德地图，又导航了回去的路线，又是走了好远的距离才到了公交站牌，我在想每天都是这样很长时间的路程肯定是扛不住的，决定是一定要坐地铁了，虽然坐地铁也是很长时间吧，但比起公交来讲，省了很长的时间和路程加上精力，我又一次深深的叹了一口气！！！
                            </p>
                        </blockquote>
                        <h4>打道回府</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                从大厦出来后，随着高德地图的导航来到了一个公交站牌，此时我才刚发现，我现在处在的位置是百度大厦，没有想到我现在的位置竟然是中国很牛逼的公司大厦下，上了车之后，我和张云鹏打电话说一下我今天的情况，他说我要的这个工资是刚刚好，作为一个专科生，而且还是应届生直接出来面试，能拿到这个薪资也是不错的，他让我再继续面试，看还有没有更好的结果，此时，我心里参杂着欣喜还有些不爽，我心里很是徘徊，一方面想着这个地方实在是太远，又一方面想着其他公司也不是好面试的，薪资基本都是这个价了，毕竟自己是刚出来，默默的从心里窜出三个字，随缘吧。走到中间换乘的时候，就突然偶遇到我室友也在等同一辆车回去，看着他面无表情，就感觉也不是太顺利，不过我们都是打着试着的心态去面试，没有成功，也不是太难受，心态问题还是挺重要的，一天天的即使没有成功，但我们也是积累着经验的，再一个就是不要灰心，说不定哪一天运气就来了，我把我今天的经历也给室友说了，也说了一些面试的技巧，在他们看来，我已经通过了这次的面试，他们感觉都挺好的， 但我实在不知道该说些什么，我还想再继续面试，在那个公司的时候，人事给我说过会会给我打电话，我也一直在等，希望是能先把这份offer拿到手，我还是接着继续面试其他公司，如果能成功的话，我再考虑考虑，如果你能成功的话，我就直接进这家公司，也是没有办法的，今天是周四，明天周五也有一家，也是在南山区，一样的感觉，面试只是经历，结果随天了。
                            </p>
                        </blockquote>
                        <h4>继续面试</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                今天周五，老郭俺俩的面试都是在南山区，今天把姓王的也给叫过来了，玩上一天，顺便请教请教经验，上午又是坐了许久的公交车，先是直接到我面试的地方，南山什么什么云谷，搞的挺洋气的一样，没什么卵用，当然老郭的是在南山的科技园，都是下午的面试，吃完饭以后，我进去面试了，让我等了好久，说好的是一点半，愣是让我等了半个多小时，其实他们是两点上班，但不知道为什么让我一点半去面试，这不是扯淡了吗，真的是不想鸟他们公司，进去之后，有一个前台小姐姐，长得还挺好看，不一会，来了一个面试官，让我去会议室进行面试，令我没有想到的是他问我的问题还都挺另类的，是专门做h5动画的，但是我对于这方面，还真是不熟，关于h5的动画我做的确实不是太多，基本都是写一些实用性的项目，他们这公司完全就是给别人搞宣传，做一些广告的动画图，我不会，我承认我不会，我也不和他怼什么，我就默默的听他讲，谁知道他一讲就是半个多小时，听的我是真难受，第一次见这么徐吊的，不管了，我就这么听下去，有些话他说了等于是放屁一样，就不能直接入主题，到最后还是来了一句，你期待的薪资是多少，让我回去等通知，你他么的早一点说不完事了，等到最后还是等到的这一句话，我之前听你那么多的屁话有什么用，真的是浪费我感情，出来之后，这个姓王的就已经在楼下等了我好久，我也不管那么多了，俺俩先去找一个地方玩一会，等着老郭，毕竟我现在也是在等我昨天的那个offer，做了一辆公交车后，我们俩绕到了塘朗山下，准备在塘朗山下等着老郭，然后再一起玩，俺俩再路边等了好大一会，我也等的迫不及待，我给昨天的那个公司打电话问他一下什么时候发offer，我好去准备一下，当然这并不是我的目的，我的目的是我在这里到底有没有被录取，我打了电话确认了一下，说的是要准备给我打电话，看来是赶到一块去了，然后又问我许多的问题，然后就把电话挂了，然后就直接给我发来了一份offer到我的邮箱里，我仔细看了看offer，心里也是暗暗自喜，因为这是我人生中的第一份offer，我决定存一个档，就这样，我们在塘朗山下走了好大一会，才碰见老郭，当时我已经是累的不行，碰面之后，我们去了一趟图书馆，我也没有什么想看的，天生不爱看书，密密麻麻，字可多，可烦，然后我就拿了一本书假装看了一会，看了一会之后，感觉没有什么意思了，我们三个也准备出去了，再去其他的地方转一转，对面刚好有一个商场，我们上去逛了一逛，此时我感觉我已经是精疲力尽，我也不知道怎么回事，完后，我和老郭找到公交车坐车回去，今天是周五，回去之后好好的歇息一下，明天周六我还有一家面试。
                            </p>
                        </blockquote>
                        <h4>继续面试2</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                今天是周六，上午有一家面试，是在龙岗区，离的也有点距离，二话不说，坐上公交车直接就是出发，到了地方之后看了一下，他么的这地方也是有点荒凉，不知道这边是干嘛的，我没有管那么多，直接就进去了，进去之后更他么的让我吃惊，他么的这公司就3个人，还能不能再可怜点，而且面试的时候还是一次性两个面试官，除了这两个面试官之外，就只有一个人在上班了，这玩个屁啊，一上来就问我那么另类的问题，我他么一个前端的，让我那么懂数据库的语言，我直接说我不会，又能怎么着，还问我有没有公司录用我，有没有公司录用我管你们什么吊事，再一次的询问，不行就是不行，哪来的那么多的废话，终于我说了一些结尾的话，赶紧撤，一刻都不想待，况且我要的工资也不高，你们他么的还想要个资历深的，有资历深的也不去你们这样的公司，就能让人一下干死的那种，扯淡吧，迈起大步，直接走出去，出去之后，找个没人的地方尿了个尿，准备起步回去，此时，结束了我这次的所有的面试，其实在下周一还有一家公司，但我真的不想去了，决定就这一家了，深圳秒加，继续等回去的公交车，准备耍个几天然后去上班，今天已经是周六了，还有三天的时间，周三让我去入职，但我还有准备工作，要去办一个工商银行卡，还有毕业证复印件，还要去医院做一个入职体检，事情还真是挺多的。
                            </p>
                        </blockquote>
                        <h4>面试结束</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                回去之后，他们都在宿舍，此时，李德生还在赶来深圳的路上，他是到下午才能来到，在宿舍还是一直的玩，到了下午，李德生快到了，我下楼去接他，看他已经是精疲力尽了，我也是体会过他的那种感觉，我帮他拉着行李箱，上了楼，等他收拾收拾，然后下去一起去吃个饭，这次出去他还要买一点生活用品，所以我们决定去远一点的地方吃饭，走了很远，我们来到了一个小餐馆，随便的要了点面，吃完以后，他们想去附近的一个什么什么山，我感觉李德生都不带累的，手里还惦着一提纸，我还穿着一个拖鞋，然后我们就去上山了，走着不一会，我们到了山脚下，这是一个盘山公路，要绕着走很远才能走到山顶，走了一会就能听到各种生物的叫声，真的是很刺激，不知道是走了多久，走到了一个小溪的地方，这地方的人还是挺多的，我们接着往前继续走，看着别人都已经从上面下来了，只有我们几个往上面去走，不得不说，这个是真的长，走的好大一会，才只是走到了半山腰，越往上面灯光就越少，信号就越差劲，于是我们决定不去了，当时已经是晚上10点多了，渴的要死，我们找到了一个有路标的地方，我们找着下去的路，我们不再上去了，大半夜的，说不定的上面有是什么吓人的东西，这时候，巡山的好像开始赶人了，已经到了时间了，我们几个一路小跑，但我穿得拖鞋，根本没法跑，只有慢慢走，快走到山尾的时候，看见了一个萤火虫，人生第一次看见萤火虫啊，很亮，很绿的那种，我就拿起手机拍，还是拍不到，可能手机像太垃圾了吧，此时已经是渴到尽头了，赶紧出山，去超市买点喝的东西，我不认识路，全是他们几个带的，走了一会，走到了住的宿舍旁边的楼下了，赶紧的去超市买点喝的，4块钱一瓶的大怡宝，二话不说，直接抱起就喝，回到宿舍，现在已经是晚上11点多了，然后点起一根烟，很爽很爽，冲了凉水澡上床睡觉，躺到床上，回想着现在已经面试成功的公司，我不知道到底要不要去，还有两天就要入职了，已经没有时间了，就这样吧，决定明天去做体检。
                            </p>
                        </blockquote>
                        <h4>入职体检</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                今天一大早的就起床，为的是赶紧体检完毕，因为体检的人很多，可想深圳每天要入职的人有多少了，体检项也很多，最麻烦的就是胸透了，一开始都找不到地方，走着走着问了好多人，才知道在哪里做，做完之后，说的是下午3点才能拿报告，我现在也闲着没事干，入职还需要一张银行卡，工商的，我建设，中国银行，邮政的都有就是没有工商的，真的很操蛋，在这个陌生的地方，根本不知道哪里有办卡的地方，还是工商的，我导航了一下，虽然离的不远，但是没有公交，只能一点一点的走过去了，大热天的，走得很烦，到了银行之后，只有自主办卡，旁边还有工作人员，指导，根据工作人员的指示，我很快的办好了，但是还需要激活，此时已经有一个人在激活银行卡，我只能等一会，过了一大会，他么的那哪是一会，这个人也太慢了吧，跟一个文盲似得，终于到我了，我一会的功夫全办好了，然后有一个人向我介绍什么什么证券啥的，我也不懂，反正这样的银行，他们也不会骗人，只是多赚点钱罢了，让我下载一个app，没啥用，此过程略过，我是在不想在这待了，不过这里还是很凉快的，但是现在离下午拿结果的时间还很长，我还是回家等吧，找到回去的公交车，到家之后，吃了点饭，决定打两把游戏，然后再去拿体检报告，老路线，去拿报告，下午3点的太阳，还是很热的，到了医院之后，拿到报告一看，啥事没有，回家吧，报告已经完事了，晚上吃饭去做毕业证复印件等等，当时说的还是需要离职证明，于是我们几个学计算机的，根本难不倒我们，直接一个网站，输入公司的名称，就可以拿到印章的图片，然后在word文档编辑出一个离职的证明，把印章图片直接插入就行了，谁也看不出来，搞好之后已打印，简直和真的一毛一样，万事俱备，只欠入职。
                            </p>
                        </blockquote>
                        <h4>初次入职</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                今天是2020.08.19，第一天入职，心情难免有点激动，今天一大早的爬起来，因为离的有点远，一个多小时的地铁才能到，6点多我就起床了，到楼下买了点吃的，一个鸡蛋，一碗豆浆，吃的不是很爽，没有往常的胡辣汤加豆腐脑两掺，不过算了，在大学的时候，早上根本没有吃饭的时候，都是一觉睡到中午，发现上班开始，早上就能吃早餐了，虽然过程很痛苦，但对身体还是有好处的，吃完之后，等公交，还好是起的早，还能挤的上公交，因为地铁站离住的地方也是有很远的距离，能走过去，但时间很长，还是做公交吧，关于路程，前一天已经熟悉了一遍，不用一点一点的再找了，到了公司之后，才8点多，是不是来的有点早了，当时公司里边只有一个人，好像是资源部的，咱也不懂是干嘛的，他问我是哪个部门的，我当时也不知道是啥部门，于是我直接说是研发部的，对于大多数公司的程序猿部门不都是研发部吗，他让我做一会等着，然后来了几个研发部的人，他们给我找了一个位置，这里就是我工作的位置了，打开电脑发现，这个电脑根本就不是研发部人用的，一定是从其他地方搬来的，我也不知道是哪的，我也不知道当时这个电脑里面的很多东西是干嘛用的，我也不敢删，反正和我工作都是无关的，先不卸载吧，等了解了再说，过一会，人事部的前台找我要证件，我就把准备好的证件给了他，我当时还在想，我面试的时候说的是19年毕业，但是毕业证上面是20年毕业，她看了一会，她似乎猜到了，也似乎没猜到，就这样看了看过去了，办完手续之后，回到自己工作的地方， 此时部门经理来了，当时就是他面试的我，他看到我，一下就知道了，然后就把我叫过去谈话，问我有没有什么压力，压力虽然在当时是没有的，但是我根本不知道一开始我要干嘛，只能等他们安排，我先把电脑上我能用到的东西先安装一遍，环境先搭配好，顺便练练手，拿出之前在培训的时候的项目，自己再重新做一遍，请求数据啥玩意的，就这样一上午过去了，旁边一个前端和一个后端带我去吃饭，就是吃快餐，当时我还不知道啥是快餐，到了地方才知道，就是吃米饭加点菜，虽然是这样很垃圾的饭，但是还是有很多人去吃的，排着队，看着自己想吃的菜，发现是真的贵，菜不是很好吃，选完之后，将近20了，第一次去这里吃饭，不知道这里的规矩，不知道怎么选划算，就这样吃吧，回去之后，该午休了，午休时间到1点半，就开始上班，我当时啥装备没有，睡觉是很冷的，就决定不睡了，玩就行了，下午我还是依旧的去写我之前锻炼的项目，熟悉熟悉，到了下午下班，他们说我没什么事就可以先回去了，我就直接打了卡下班了，到了地铁之后，歪日，现在下班的人是真的多，一开始还不挤，走了两三站就人已经很多了，就是两只脚站的地方，下了地铁之后，不做公交了，反正是不赶时间，慢慢的走就行了，今天还是下班就走了，下了地铁之后，已经是8点了，我再走回去，差不多8点半了，去一个盖浇饭的地方吃了点饭，然后回住的地方了，就和他们分享今天是怎么过去的，入职的第一天就这样过去了。
                            </p>
                        </blockquote>
                        <h4>工作准备</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                入职手续已办理结束，今天开始，就要开始工作了，刚开始肯定不让我做那么多的东西，因为我一开始就是个菜鸟，只懂得一些代码的用法，对于公司里面到底怎样去开发，这个还需要几天的磨练，今天他们让我把前端的环境配置，所有前端用的东西准备好之后，就从git上拉下来一个项目，让我去看，代码拉下来后，歪日，这东西看起来真是烧脑子，从来没有见过这么多的代码文件，但对于他们说，这已经是最简单的了，就发现自己在培训出来之后啥也不是，没办法，对着这陌生的代码，只能慢慢的去看，毕竟里面的组件没有用过，就是iview，真的是第一次见，硬着头皮去看，看了一上午，感觉成果不大，只是熟悉了代码，然后经理让我做一个关于后台管理的增删改查，这点小小的功能，对我老说，是有难度的，因为我遇见这样的情况，我是啥也不会，只能去看这个项目里面类似的代码，就这我稀里糊涂的也写不上来，写了半天，才弄出一个弹框，里面的输入框还没有整明白呢，加上表单的验证，真的是给我整懵逼了，完事之后，我就让经理去看，写了这仅仅几个功能，全是bug，好多共功能整的不是很明白，全是经理给我参谋着，我才写出来，弹框整完后，我把代码再熟悉一遍逻辑，似乎大概也能懂了，感觉着这就是收获，慢慢的积累，经理说马上就开始做一个微信公众号了，让我去做准备，在我的记忆中，我根本就没写过公众号，只写过小程序网页，咱也不知道是怎么写，两天过去了，就到了星期天，在宿舍的他们几个有的还没有面试成功，然后我就可以教他们一点东西，面试的技巧，星期两天就在家好好休息就行了。
                            </p>
                        </blockquote>
                        <h4>公众号准备</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                入职大约有一段时间了，要开始新的项目了，一开始我以为微信公众号是直接那种用微信开发的，谁知道一了解，公众号也是网页，所以也能用vue去开发，那不就简单多了，看到经理给我搭的脚手架，就感觉这脚手架也挺难的，里面好多配置的，根本都看不明白是什么东西，先不管那么多了，先从页面开始就行，产品给我发来原型图，首次开发项目的我根本不知道原型图是啥，不过没关系，不会就是不会，不会就问就完事了，对于一开始做项目，还是不知道一个项目是怎么从产品到前端开发，中间再加一个ui，那时候的我只觉得太正式了，一句话所有的页面全部按照原型图来，首先做的是登录页面，因为在学校学的时候，老师就说，一定要按照ui给的图去做，就连尺寸大小都是一模一样的，所以当时就特别的认真，导致几个页面做了好几天，不过做出来的效果还是很好的，在一个好的是经理没有催我的进度，他知道我一开始做的也是很慢，所以就给我足够的时间，虽然页面是按照原型图来做，但是到后来才知道，根本不用做那么仔细，对于一个新手，在一个他们不会对这个项目要求的太高，只要能做出来就行，慢慢的后来，我了解到了里边的偷懒机制，我就不做那么仔细了，大差不差就行，一段时间的努力，终于页面是写的差不多了，后面也开始慢慢的对接口吧，简单的接口还行，还有的是数据结构是需要修改的，这就让人很头疼了，对于数据的问题，js的一些方法是一定要用的熟练，光是对接口写页面还不行，还有一些功能还是挺难的，毕竟对于功能这一块。逻辑方面不是太厉害，公众号里面的代码写的是零零散散啰里啰嗦，但我是初学者，只能慢慢的去吾了，有些地方的传值，传来传去，有时候自己都会懵掉，不管是用本地存储，还是vuex，不过当时只听尽量不要用本地存储，能用其他方法解决的，就不用本地存储，其实听这样的话一点都没有用，哪个方法好用就用哪个就行了，能解决问题都是好前端，管他那么多，最后难点就是地图了，那时候地图的渲染编写一点不会，更不要说里面的api怎么用了，那时还是用的百度地图，最后，经过一个多月的开发，终于算是开发完毕了，但这已经超过了公司给安排的时间，不过不怕，就拖下去怎么了，人有多大胆，就有多大产。公众号的开发就这样告一段落了。
                            </p>
                        </blockquote>
                        <h4>气贸准备</h4>
                        <blockquote>
                            <p style="text-indent: 2em;">
                                公众号结束后，
                            </p>
                        </blockquote>
                        <pre><code> 未完待续~~~</code></pre>
