<script>
    import Banner from '@/components/banner'
    import sectionTitle from '@/components/section-title'
    import menuTree from '@/components/menu-tree'
    import articlePassword from '@/components/articlePassword'
    import './css/articles.less'
    export default {
        name: 'articles',
        data(){
          return{
              showDonate: false,
              menus: [],
              showArt: false,
          }
        },
        components: {
            Banner,
            sectionTitle,
            menuTree,
            articlePassword,
        },
        
        methods: {
            showArticle() {
                const that = this;
                that.showArt = true;
            },
            fetchH(arr,left,right){
                if (right) {
                    return arr.filter(item => item.offsetTop > left && item.offsetTop < right)
                }else {
                    return arr.filter(item => item.offsetTop > left)
                }
            },
            // 生成目录
            createMenus(){
                let arr = []
                for(let i=6;i>0;i--){
                    let temp = []
                    let e = document.querySelector(".entry-content").querySelectorAll(`h${i}`)
                    for (let j=0;j<e.length;j++){
                        let child = this.fetchH(arr,e[j].offsetTop,(j+1 === e.length)?undefined:e[j+1].offsetTop)
                        temp.push({
                            h: i,
                            title: e[j].innerText,
                            id: e[j].id,
                            offsetTop: e[j].offsetTop,
                            child
                        })
                    }
                    if (temp.length){
                        arr = temp
                    }
                }
                this.menus = arr
            }
        },
        mounted(){
            // this.createMenus()
        },
        created() {
            
        }
    }
</script>